.header{
  &__container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 100px;
    background: #F2F2F2;
    padding: 12px;
  }

  &__logo{
    width: 116.262px;
    height: 48.005px;

    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__nav-items{
    display: flex;
    align-items: center;
    gap: 32px;

    a{
      color: $black;
      font-size: $font-size;
      font-style: normal;
      font-weight: 600;
      line-height: 125%;
    }
  }

  &__btn-wrapper{
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &__btn-profile{
    width: 40px;
    height: 40px;


    svg{
      width: 100%;
      height: 100%;
      transition: all .3s;

      path{
        transition: all .3s;
      }
    }

    &:hover{
      cursor: pointer;
    }

    &:hover svg path{
      cursor: pointer;
      fill: $blue;
    }
  }
}